                                          </el-dropdown-menu>
                                      </el-dropdown>
                                  </div> -->
                              </div>
                          </el-card>
                      </el-col>
                      <el-col :span="6">
                          <el-card :body-style="{ padding: '0px' }" class="mould-card">
                              <img class="image" v-bind:src="userLogo">
                              <div style="padding: 14px;">
                                  <h4>模具A</h4>
                                  <span>说明xxxxxxx</span>
                                  <!-- <div class="bottom clearfix">
                                      <time class="time">{{ currentDate }}</time>
                                      <el-dropdown style="float: right;color: #409EFF;">
                                          <span class="el-dropdown-link"><i class="el-icon-edit"></i>
                                            </span>
                                          <el-dropdown-menu slot="dropdown">
                                              <el-dropdown-item><el-button type="text" @click="mouldInfos">详情</el-button></el-dropdown-item>
                                              <el-dropdown-item><el-button type="text" @click="delMouldRecord">废弃</el-button></el-dropdown-item>
                                          </el-dropdown-menu>
                                      </el-dropdown>
                                  </div> -->
                              </div>
                          </el-card>
                      </el-col>
                      <el-col :span="6">
                          <el-card :body-style="{ padding: '0px' }" class="mould-card">
                              <img class="image" v-bind:src="userLogo">
                              <div style="padding: 14px;">
                                  <h4>模具A</h4>
                                  <span>说明xxxxxxx</span>
                                  <!-- <div class="bottom clearfix">
                                      <time class="time">{{ currentDate }}</time>
                                      <el-dropdown style="float: right;color: #409EFF;">
                                          <span class="el-dropdown-link"><i class="el-icon-edit"></i>
                                            </span>
                                          <el-dropdown-menu slot="dropdown">
                                              <el-dropdown-item><el-button type="text" @click="mouldInfos">详情</el-button></el-dropdown-item>
                                              <el-dropdown-item><el-button type="text" @click="delMouldRecord">废弃</el-button></el-dropdown-item>
                                          </el-dropdown-menu>
                                      </el-dropdown>
                                  </div> -->
                              </div>
                          </el-card>
                      </el-col>
                  </el-row>
              </div>
          </el-card>
        </div>

        <!-- 分配明细 -->
        <div>
          <el-dialog width="60%" title="分配工作" :visible.sync="innerVisible" :close-on-click-modal="false" append-to-body>
            <el-form ref="elform" :model="elform" :rules="rules" label-width="30%" >
              <el-form-item label="选择加工流程：" prop="productName" >
                <el-select @change="test" v-model='elform.flowId' placeholder="请输选择加工流程" style="width:50%;">
                  <el-option v-for="item in restaurants3" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="选择加工人员：" prop="wenlidaima" >
                <el-select v-model="selRoles" multiple placeholder="请选择加工人员" style="width:50%;">
                  <el-option
                    v-for="ar in restaurants4"
                    :key="ar.value"
                    :label="ar.label"
                    :value="ar">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="选择质量监管人员：" prop="qualityUsers" >
                <el-select v-model="zhiliang" multiple placeholder="选择质量监管人员：" style="width:50%;">
                  <el-option
                    v-for="ar in restaurants5"
                    :key="ar.value"
                    :label="ar.label"
                    :value="ar">
                  </el-option>
                </el-select>
                <!-- <el-select v-model='elform.renyuan' placeholder="请选择主质量总监">
                  <el-option v-for="item in restaurants5" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select> -->
              </el-form-item>
              <!-- <el-form-item label="选择副质量监管人员：" prop="wenlidaima">
                <el-select v-model='elform.renyuan' placeholder="请选择主质量总监">
                  <el-option v-for="item in restaurants6" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
              </el-form-item> -->
            </el-form>
            <div style="border: 1px dashed #d9d9d9;width:100%;min-height:358px;">
                <span>{{selectFlow.flowName}}</span>
                <el-steps align-center>
                  <el-step v-for="step in selectFlow.stepslist" :title="step.stepName" :description="step.remark"></el-step>
                </el-steps>
            </div>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" @click="toFenPei">确 定</el-button>
              <el-button @click="toQuXiao">取 消</el-button>
            </div>
          </el-dialog>
        </div>
      </el-main>
    </el-container>
  </div>
</div>
</template>
<script>
import VueCoreImageUpload from 'vue-core-image-upload';
export default {
  data() {
    return {
      techId: false,
      liuCheng:[],
      userLogo:'',
      udapteBoolean: false,
      elform:{},
      zhiliang:[],
      selRoles: [],
      allRoles: [],
      selRolesBak: [],
      innerVisible: false,
      urlId:'',
      qrCode:'',
      imageUrl: '',
      contr_loading: false,
      base_loading: false,

      exform: {
        addUserId: '',
        addUserName: '',
        orderId: '',
        qiKong: '无',
        queKou: '无',
        lieFeng: '无',
        hanBa: '无',
        dianHuoHua: '无',
        youShiSi: '无',
        heMoBian: '0',
        fenXingBian: '0',
        tongGZ: false,
        zuiHouShiMoJian: '0',
        ckd: '0',
        guangJian: '0'
      },
      flowData:[],
      filterText: '',
      tree_loading: false,
      item_order_id: '2',
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      select_part: [],
      data3: [],
      nowTab: 1,
      username: '',
      name: '',
      uid: '',
      selectFlow:{},
      imageUrls: [],
      restaurants1: [],
      restaurants2: [],
      restaurants3: [],
      restaurants4: [],
      restaurants5: [],
      restaurants6: [],
      imageData: {
        userId: '',
        imgPakage: 'order_pm'
      },
      ruleForm: {
        orderName: '',
        addUserId: '',
        addUserName: '',
        projectId: '',
        hasRemark: false,
        picUrls: '',
        urgency: '0',
        remark: '',
        realityArea: '0',
        machiningType: ''
      },
      restaurants: [],
      rules: {
        orderName: [{
            required: true,
            message: '请输入订单名称',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 0 到 255 个字符',
            trigger: 'blur'
          }
        ],
        machiningType: [{
          required: true,
          message: '请选择工艺类型',
          trigger: 'blur'
        }],
        managerId: [{
          required: true,
          message: '请选择车间负责人',
          trigger: 'blur'
        }],
        realityArea: [{
          required: true,
          message: '请输入加工面积',
          trigger: 'blur'
        }],
        projectId: [{
          required: true,
          message: '请选择所属项目',
          trigger: 'blur'
        }],
        urgency: [{
          required: true,
          message: '请选择订单紧急成度',
          trigger: 'blur'
        }],

      }
    }
  },
  components: {
    VueCoreImageUpload
  },
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val);
    }
  },
  methods: {
    test(item){
      let self = this;
      for(let i = 0;i< self.flowData.length;i++){
        if(item === self.flowData[i].id){
          self.selectFlow = self.flowData[i];
        }
      }
    },
    toQuXiao(){
        this.innerVisible = false;
        this.resetForm('elform');
    },
    fenPei(){
      this.selectFlow={};
      this.elform = {}
      this.selRoles = [];
      this.zhiliang = [];
      this.innerVisible = true;
    },
    toFenPei(){
      let self = this;
      let ygas = [];
      if(this.selRoles.length == 0){
        self.$message.info("至少选择一位工艺师");
        return
      }
      for(let i = 0; i<this.selRoles.length; i++){
        let id = this.selRoles[i].value;
        let name = this.selRoles[i].label;
        let yga = {
          id: id,
          name:name
        }
        ygas.push(yga);
      }

      let qu = []
      if(this.zhiliang.length == 0){
        self.$message.info("至少选择一位质量总监");
        return
      }
      for(let i = 0; i<this.zhiliang.length; i++){
        let id = this.zhiliang[i].value;
        let name = this.zhiliang[i].label;
        let zi = {
          id: id,
          name:name
        }
        qu.push(zi);
      }

      let params = {
        orderId: this.urlId,
        addUserId: this.uid,
        addUserName: this.name,
        nanDuXiShu: 2,
        ygas: ygas,
        qualityUsers: qu,
        flow: this.selectFlow,
      }
      this.jsonPostRequest("/plant/fen/pei", params).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success("分配任务成功")
          this.$router.push('/plant/new/order')
        } else {
          self.$message.error("分配任务失败")
        }
      });
    },

    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleSelect(item) {
    },
    loadAllliucheng() {
      let self = this
      this.getRequest("/work/flow/list/steps").then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          let item = []
          let morens = resp.data.data.flowlist
          self.flowData = morens;
          if (morens != 'undefined' && morens.length > 0) {
            for (var i = 0; i < morens.length; i++) {
              let elem = {
                label: '',
                value: ''
              }
              elem.label = morens[i].flowName
              elem.value = morens[i].id
              item.push(elem);
            }
            self.restaurants3 = item;
          }
        } else {
          console.log("获取列表数据失败");
        }
      });
    },
    loadAllYuanGongA() {
      let self = this
      this.getRequest("/order/list/mangers?role=ROLE_yga").then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          let item = []
          let re = resp.data.data;
          if (re != 'undefined' && re.length > 0) {
            for (var i = 0; i < re.length; i++) {
              let elem = {
                label: '',
                value: ''
              }
              elem.label = re[i].name
              elem.value = re[i].id
              item.push(elem);
            }
            self.restaurants4 = item;
          }
        } else {
          console.log("获取列表数据失败");
        }
      });
    },
    loadAllZhiliang() {
      let self = this
      this.getRequest("/order/list/mangers?role=ROLE_zhiliang").then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          let item = []
          let re = resp.data.data;
          if (re != 'undefined' && re.length > 0) {
            for (var i = 0; i < re.length; i++) {
              let elem = {
                label: '',
                value: ''
              }
              elem.label = re[i].name
              elem.value = re[i].id
              item.push(elem);
            }
            self.restaurants5 = item;
          }
        } else {
          console.log("获取列表数据失败");
        }
      });
    },
    getOrderInfo(urlId){
      let self = this;
      this.getRequest("/order/list/info?orderId=" + urlId).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.ruleForm = resp.data.data.orderinfo;
          this.techId = resp.data.data.orderinfo.techId;
          this.qrCode = resp.data.data.orderinfo.qrCode;
          this.exform = resp.data.data.orderinfo.controlOrderFrom;
          let moulds = resp.data.data.orderinfo.mouldInfoList;
          if('undefined' != moulds && moulds.length > 0){
            for(let i= 0;i< moulds.length;i++){
              let part = {
                collectPartinfoEntity:{}
              }
              part.collectPartinfoEntity = moulds[i];
              this.addSelected(part);
            }
          }
        } else {
          console.log("获取订单详情失败");
        }
      });
    },

  },
  mounted: function() {
    this.$nextTick(function() {
    })
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.loadAllliucheng();
    this.loadAllYuanGongA();
    this.loadAllZhiliang();
  },
  created: function() {
    this.urlId = this.$route.params.id;
    this.getOrderInfo(this.urlId);
  }
}
</script>
<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.el-select .el-input {
  width: 130px;
}

.card-bottom {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: right;
}

.input-with-select .el-input-group__prepend {
  background-color: #fff;
}

.card-bottom-left {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: left;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.mould-card {
  margin-bottom: 15px;
}

.mould-card:hover {
  transform: translateY(-2%);
  box-shadow: 1px 4px 10px 2px #CCC;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.pre-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
}
</style>



